.layout .pic-details-demo-header {
  height: 32px;
  line-height: 32px;
  background: #000;
}
.layout .pic-details-demo-header ul li {
  list-style: none;
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 4px;
  margin: 0 5px 0 0;
}
.layout .pic-details-demo-header ul li:nth-child(1) {
  background: #CF3333;
  margin-left: 3%;
}
.layout .pic-details-demo-header ul li:nth-child(2) {
  background: #DEC811;
}
.layout .pic-details-demo-header ul li:nth-child(3) {
  background: #28C026;
}
.layout .pic-details-demo-header ul li:nth-child(4) {
  background: #fff;
  width: 80%;
  height: 16px;
  vertical-align: middle;
  margin: 0 10px;
}
.layout .pic-details-demo-header ul li:nth-child(5),
.layout .pic-details-demo-header ul li:nth-child(5):before,
.layout .pic-details-demo-header ul li:nth-child(5):after {
  width: 20px;
  height: 2px;
  background: #BABABA;
  border-radius: 2px;
}
.layout .pic-details-demo-header ul li:nth-child(5) {
  vertical-align: middle;
  margin-left: 5px;
  position: relative;
}
.layout .pic-details-demo-header ul li:nth-child(5):before,
.layout .pic-details-demo-header ul li:nth-child(5):after {
  content: '';
  display: block;
  position: absolute;
}
.layout .pic-details-demo-header ul li:nth-child(5):before {
  top: -6px;
}
.layout .pic-details-demo-header ul li:nth-child(5):after {
  top: 6px;
}
